import { useState } from 'react';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import {
  Form,
  FormGroup,
  Label,
  Button,
  Input,
  FormText,
} from 'reactstrap';
import { userAuthThunk } from '../../../../redux/actions/AuthAction';

function UserForm() {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const [form, setForm] = useState({});
  const [loginToggle, setLoginToggle] = useState(false);

  const handleForm = () => {
    setLoginToggle(!loginToggle);
  };

  const handleChange = (e) => setForm((prev) => ({ ...prev, [e.target.name]: e.target.value }));

  const handleSubmit = (e) => {
    e.preventDefault();
    dispatch(userAuthThunk(loginToggle, form, navigate));
    setForm({});
    navigate('/');
  };

  return (
    <div className="userForm w-75 m-auto" style={{ paddingTop: '20px' }}>
      <Form onSubmit={handleSubmit}>
        <FormGroup className="mb-3" controlId="formBasicName">
          <Label></Label>
          <Input
            type="text"
            value={form.name || ''}
            name="name"
            disabled={loginToggle}
            onChange={handleChange}
            placeholder="Name"
            required
            pattern="[A-Z-a-z-]{1,30}"
            title="            30 "
          />
        </FormGroup>

        <FormGroup className="mb-3" controlId="formBasicEmail">
          <Label>Email</Label>
          <Input
            type="text"
            value={form.email || ''}
            name="email"
            onChange={handleChange}
            placeholder="Email"
            required
            pattern="^[\w]{1,}[\w.+-]{0,}@[\w-]{2,}([.][a-zA-Z]{2,}|[.][\w-]{2,}[.][a-zA-Z]{2,})$"
            title=" email    user@gmail.com"
          />
          <FormText className="text-muted" />
        </FormGroup>

        <FormGroup className="mb-3" controlId="formBasicPassword">
          <Label></Label>
          <Input
            type="password"
            value={form.password || ''}
            name="password"
            onChange={handleChange}
            placeholder="Password"
            required
            pattern="(?=(?:.*[A-Za-z]){2})(?=(?:.*[0-9]){4})^.{6,}$"
            title="    2   4 "
          />
        </FormGroup>
        <FormGroup className="mb-3" controlId="formBasicCheckbox">
          <Input type="checkbox" onChange={handleForm} />
          <span>{loginToggle ? '  ' : ''}</span>
        </FormGroup>
        <FormGroup className="mb-3" controlId="formBasicCheckbox">
          <Input type="checkbox" />
          <span> </span>
        </FormGroup>
        <Button variant="primary" type="submit">
          
        </Button>
      </Form>
    </div>
  );
}

export default UserForm;
